import styles from "./page.module.css";

// Mock project data
const mockProjects = [
  {
    id: "1",
    name: "January Shorts Batch",
    templateName: "YouTube Shorts Template",
    status: "completed",
    createdAt: new Date("2024-01-06"),
    itemCount: 5,
  },
  {
    id: "2",
    name: "Product Launch Videos",
    templateName: "Product Showcase",
    status: "in_progress",
    createdAt: new Date("2024-01-05"),
    itemCount: 3,
  },
  {
    id: "3",
    name: "TikTok Series",
    templateName: "TikTok Hook Template",
    status: "pending",
    createdAt: new Date("2024-01-04"),
    itemCount: 10,
  },
];

const statusColors = {
  completed: "#22c55e",
  in_progress: "#f59e0b",
  pending: "#6b7280",
  failed: "#ef4444",
};

const statusLabels = {
  completed: "Completed",
  in_progress: "In Progress",
  pending: "Pending",
  failed: "Failed",
};

export default function ProjectsPage() {
  return (
    <div className={styles.container}>
      <header className={styles.header}>
        <div>
          <h1 className={styles.title}>Projects</h1>
          <p className={styles.subtitle}>
            Generated videos from your templates
          </p>
        </div>
        <button className={styles.createBtn}>
          <span>+</span>
          <span>New Project</span>
        </button>
      </header>

      <div className={styles.tableContainer}>
        <table className={styles.table}>
          <thead>
            <tr>
              <th>Project Name</th>
              <th>Template</th>
              <th>Items</th>
              <th>Status</th>
              <th>Created</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {mockProjects.map((project) => (
              <tr key={project.id}>
                <td>
                  <span className={styles.projectName}>{project.name}</span>
                </td>
                <td className={styles.templateName}>{project.templateName}</td>
                <td>{project.itemCount} videos</td>
                <td>
                  <span
                    className={styles.status}
                    style={{
                      background: `${statusColors[project.status as keyof typeof statusColors]}20`,
                      color: statusColors[project.status as keyof typeof statusColors],
                    }}
                  >
                    {statusLabels[project.status as keyof typeof statusLabels]}
                  </span>
                </td>
                <td className={styles.date}>
                  {project.createdAt.toLocaleDateString()}
                </td>
                <td>
                  <div className={styles.actions}>
                    <button className={styles.actionBtn} title="Open in OpenCut">
                      üöÄ
                    </button>
                    <button className={styles.actionBtn} title="Download">
                      ‚¨áÔ∏è
                    </button>
                    <button className={styles.actionBtn} title="Delete">
                      üóëÔ∏è
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {mockProjects.length === 0 && (
        <div className={styles.emptyState}>
          <span className={styles.emptyIcon}>üìÅ</span>
          <h3>No projects yet</h3>
          <p>Create a project by applying a template with your content</p>
        </div>
      )}
    </div>
  );
}
